@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  list-style: none;
  text-decoration: none;
}

body {
  margin: 0;
  padding: 0;
}

.info {
  height: 100%;
  width: 100%;
  min-height: 100vh;
  background: linear-gradient(90deg, #000 39.06%, rgba(0, 250, 254, 0.50) 100%);
  position: relative;
  display: flex; 
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 0 5%;
  transition: .2s;
  gap: 25rem;
}

.img-logo img {
  width: 90px;
  height: auto;
  display: flex;
 
}

.animation {
    width: 40%;
    height: auto;
}

.text-container {
  flex: 1;
  max-width: 500px; 
}

.text {
  max-width: 500px;
  
}

.text h1 {
  font-size: 30px;
  font-weight: 700;
  color: #FFF;
  margin-bottom: 10px;
  word-wrap: break-word;
}

.mobix {
  color: rgba(4, 189, 202, 0.98);
  animation: flashAnimation 5s linear infinite;
}

@keyframes flashAnimation {
  0% {
    color: rgba(4, 189, 202, 1.00);
  }
  50% {
    color: #FFF;
  }
  100% {
    color: rgba(0, 250, 254, 0.50);
  }
}

.text p {
  font-size: 20px;
  font-weight: 400;
  color: #FFF;
  margin: 0 0 30px;
}

.info a {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  width: 80%;
  color: white;
  background-color: rgba(2, 146, 156, 0.98);
  border: 1px solid transparent;
  padding: 20px 30px;
  line-height: 14px;
  font-size: 18px;
  font-weight: 600;
  border-radius: 30px;
  text-transform: uppercase;
  transition: background-color 0.5s ease, border-color 0.3s ease;
}

.info a:hover {
  background: transparent;
  color: #FFF;
  text-decoration: none;
  border: 1px solid white;
}

.screens-imgs {
  display: flex;
  flex-direction: row;
  flex: 1;
  justify-content: end;
  
}

.screens-imgs img {
  width: 50%;
  height: auto;
  max-width: 100%;
  object-fit: cover;
  margin-right: 30px;
  border-radius: 30px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
  animation: floatingAnimation 5s ease-in-out infinite alternate;
}

@keyframes floatingAnimation {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0);
  }
}

.Carousel {
  display: none;

}
 
@media (max-width: 1650px) {
